@import url(//fonts.googleapis.com/css?family=Oswald:400);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
background-color: white;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
nav.navbar {
    margin-bottom: 0;
}
div.thumbnail {
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    /* width: 100%; */
    padding-top: 100%;
}
.view {
    margin-right: 3px
}
.search {
    float: right;
    margin: 10px 0;
}

.jumbotron {
    min-height: 300px;
    padding-top: 100px;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(61,99,246, .5)),  color-stop(rgba(0,196,204 )),to(rgba(107,213,227)) );
    background-image: -webkit-linear-gradient(left, rgba(61,99,246, .5),  rgba(0,196,204 ),rgba(107,213,227) );
    background-image: linear-gradient(to right, rgba(61,99,246, .5),  rgba(0,196,204 ),rgba(107,213,227) );
    font-family: "Oswald", sans-serif;
    text-align: center;
}
